.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  color: #7ceef6;
  border: #ff8c00 solid 1px;
  border-radius: 8px;
  margin: 40px;
  padding: 40px;

  h2 {
    font-size: 40px;
  }

  .list {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    flex: 1;
    color: #d41075;

    // &:nth-child(1),
    // &:nth-child(2) {
    //   flex-basis: 45%;
    // }
  }
  .img {
    max-height: 400px;
    padding: 20px;
    border-radius: 28px;
    box-shadow: -10px -10px 15px rgba(87, 71, 71, 0.5),
      10px 20px 15px rgba(103, 213, 240, 0.12);
  }
}

button {
  padding: 10px;
  margin: 20px;
  background-color: #ff8c00;
  color: #7ceef6;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  font-size: 1em;
  box-shadow: inset 20px 5px 20px 2px rgba(0, 0, 0, 0.25);

  &:hover {
    background-color: #d41075;
    transform: translateY(1px);
    box-shadow: inset 0px 10px 10px 0px rgba(0, 0, 0, 0.326);
  }
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 40px;
  padding: 20px;
}
